import { useEffect, useState } from 'react'
import { useParams } from 'react-router'

// Note: `uuid` comes from the URL, courtesy of our router
const Agent = () => {
  const [data, setData] = useState({})
  const [ablities, setAblities] = useState([])
  const [role, setRole] = useState({})
  const { uuid } = useParams()
  
  const getData = async () => {
    const res = await fetch(`https://valorant-api.com/v1/agents/${uuid}`)
    const respdata = await res.json()
    console.log(respdata.data)
    setData(respdata.data)
    setAblities(respdata.data.abilities)
    setRole(respdata.data.role)
    const audio = new Audio(`https://media.valorant-api.com/sounds/${respdata.data.voiceLine.mediaList[0].id}.wav`)
    await audio.play();
  }

  useEffect(() => {
    getData();
  }, [])

  const { displayName, bustPortrait, displayIcon } = data

  return (
    <main className="agent">
      <div className="imgBox"  style={{backgroundImage:`url(${displayIcon})`}}>
        <div className="bustPicture" style={{backgroundImage:`url(${bustPortrait})`}}>
          {/* make Name and Role tag with some effect and img background color*/}
          <h1 className='name'>{displayName}
            <br />
            <span>{role.displayName}</span>
          </h1>
        </div>
      </div>
      <div className="contentBox">
        <div className="infoBox">
          <h2>// INFO</h2>
          <p>{data.description}</p>
        </div>
        <div className="abilitiesBox">
          <h2>// ABILITIES</h2>
          <ul className="abilities-list">
            {
              ablities.map(abl=>{
                const { displayName, description,displayIcon } = abl
                if(displayIcon)
                return(
                  <li className='ability'>
                    <div className="imgAbl" style={{backgroundImage:`url(${displayIcon})`}}>
                    </div>
                    <div className="descAbl">
                      <h3>{displayName}</h3>
                      <p>{description}</p>
                    </div>
                  </li>
                )
              })
            }
          </ul>
        </div>
      </div>
    </main>
  )
}

export default Agent
